body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  padding: 0px;
  margin: 0px;
  height: 300vh;
  font-family: "Lucida Console"
}

.rootLayout {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0px;
  padding: 0px;
  height: 100vh;
  gap: 1em;
  /*position: fixed;*/

}



.image {
  background-color: blueviolet;
  grid-row: 1/3;
  grid-column: 1/3;
}

.content-1 {
  background-color: chocolate;
  grid-column: 3;
  grid-row: 1/2;
  padding: 10px;

  border-bottom-left-radius: 5px;

}

.content-2 {
  background-color: yellow;
  grid-column: 3;
  grid-row: 2/3;
  padding: 10px;
  border-top-left-radius: 5px;
}

img {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 800px) {

  .rootLayout {
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin: 0px;
      padding: 0px;
      height: 100vh;
      gap: 0.5em;

  }

  .image {
      background-color: 	rgb(255,248,220,0.6);
      grid-row: 1/2;
      grid-column: 1/4;
  }

  .content-1 {
      grid-column: 1/2;
      grid-row: 2/3;
      border-top-right-radius: 5px;
      font-size: 1.9vh;

  }

  .content-2 {
      font-weight: 100;
      grid-column: 2/4;
      grid-row: 2/3;

      font-size: 1.9vh;

  }
}

.header {

  background: url('https://images.unsplash.com/photo-1519659528534-7fd733a832a0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=926&q=80');
  height: 100vh;

  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: fixed;
  background-size: cover;

}


.landingContent {
  color: white;
  text-align: center;
  padding-top: 50vh;
  font-size: 4em;
}

.landingContent span {
  color: white;
}
.sticky {
position: fixed;
top: 0;
}